import styled from 'styled-components';

export const Header = styled.header`
    width: 100%;
    height: 18.5rem;

    border-radius: 0 0 3.125rem 3.125rem;
    
    background: no-repeat 20% 50%/170% url(${(props) => props.gifBg});
    box-shadow: 0px 17px 80px rgba(36, 68, 102, 0.49), 0px 7.10219px 33.4221px rgba(36, 68, 102, 0.352239), 0px 3.79717px 17.869px rgba(36, 68, 102, 0.292093), 0px 2.12866px 10.0172px rgba(36, 68, 102, 0.245), 0px 1.13052px 5.32008px rgba(36, 68, 102, 0.197907), 0px 0.470434px 2.21381px rgba(36, 68, 102, 0.137761);

    @media(min-width: 768px) {
        justify-content: center;
    }
    
    transition: 400ms;
`;

export const GradientHeader = styled.div`
    width: 100%;
    height: 100%;
    background-image: linear-gradient(16.5deg, rgba(82, 82, 82, 0.6) 0%, rgba(61, 114, 180, 0.6) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    border-radius: 0 0 3.125rem 3.125rem;
    
    font-weight: 400;
    font-size: 1rem;
    color: #BCBCBC;

    & strong {
        font-weight: 700;
        font-size: 1.25rem;
        color: #FFFFFF;
        margin-top: -0.25rem;
        margin-bottom: 0.375rem;
    }
    
    transition: 400ms;
`;

export const Redes = styled.div`
    width: 13.625rem;
    height: 4rem;
    padding: 0.3rem 1.875rem 0 1.875rem;
    position: absolute;
    top: 16.5rem;
    /* bottom: -2rem; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: solid 0.125rem #FFF;
    border-radius: 3.125rem;
    background-color: ${props => props.dark ? '#25252580' : '#CBCBCB80'};

    z-index: 12;
    
    transition: 400ms;
`;

export const Links = styled.div`
    width: 22rem;
    height: 4.375rem;
    padding: 0 2.5rem 0 1.875rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 3.125rem;
    margin-bottom: 2rem;
    background-color: ${props => props.dark ? '#1B1D20' : '#FFF'};

    transition: 400ms;

    box-shadow: ${props => props.dark ? '0px 51px 106px rgba(77, 89, 124, 0.1), 0px 21.3066px 44.2843px rgba(77, 89, 124, 0.0718854), 0px 11.3915px 23.6765px rgba(77, 89, 124, 0.0596107), 0px 6.38599px 13.2728px rgba(77, 89, 124, 0.05), 0px 3.39155px 7.04911px rgba(77, 89, 124, 0.0403893), 0px 1.4113px 2.93329px rgba(77, 89, 124, 0.0281146)' : '0px 51px 106px rgba(0, 0, 0, 0.1), 0px 21.3066px 44.2843px rgba(0, 0, 0, 0.0718854), 0px 11.3915px 23.6765px rgba(0, 0, 0, 0.0596107), 0px 6.38599px 13.2728px rgba(0, 0, 0, 0.05), 0px 3.39155px 7.04911px rgba(0, 0, 0, 0.0403893), 0px 1.4113px 2.93329px rgba(0, 0, 0, 0.0281146)'};

    z-index: 10;
    
    transition: 400ms;

    &:hover {
        transform: scale(1.05);
    }
`;

export const TextLinks = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;

    transition: 400ms;
    
    transition: 400ms;
`;

export const FixAccordion = styled.div`
    display: flex;
    flex-direction: column;
`;

export const TitleDiv = styled.div`
    width: 100%;
    font: 600 1.5rem Sora;
    letter-spacing: 6px;
    color: ${props => props.dark ? '#FFF' : '#000'};
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: -0.2rem;

    transition: 400ms;
    
    transition: 400ms;
`;

export const SubTitleDiv = styled.div`
    width: 100%;
    font: 400 1.125rem Sora;
    color: ${props => props.dark ? '#FFF' : '#000'};
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: -0.2rem;
    
    transition: 400ms;
`;

export const Container = styled.header`
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* justify-content: center; */
    align-items: center;
    overflow: hidden;
    
    background-color: ${props => props.dark ? "#03050B" : "#EAEAEA"};

    @media(min-width: 768px) {
        justify-content: center;
    }
    
    transition: 200ms;
`;

export const Photo = styled.img`
    width: 8rem;
    height: 8rem;
    border-radius: 50%;

    margin-bottom: 1rem;

    box-shadow: 0px 9px 138px rgba(239, 239, 239, 0.24), 0px 3.75998px 57.6531px rgba(239, 239, 239, 0.172525), 0px 2.01027px 30.8241px rgba(239, 239, 239, 0.143066), 0px 1.12694px 17.2797px rgba(239, 239, 239, 0.12), 0px 0.598509px 9.17714px rgba(239, 239, 239, 0.0969343), 0px 0.249053px 3.81881px rgba(239, 239, 239, 0.0674749);

    @media(min-width: 768px) {
        justify-content: center;
    }
    
    transition: 400ms;
`;
